import React, { useState } from 'react';
import validator from 'validator';

import './Signup.scss';
import AuthButton from '../AuthButton/AuthButton';
import InputField from '../InputFields/InputFields';

function Signup({ history }) {
  const [isValid, setIsValid] = useState('');
  const [state, setState] = useState({
    name: '',
    email: ''
  });

  function handleChange(e) {
    setIsValid('');

    const { name, value } = e.target;
    setState(pstate => ({
      ...pstate,
      [name]: value
    }));
  }

  function handleSubmit(e) {
    e.preventDefault();

    let {name, email} = state;
    email = email.toLocaleLowerCase();
    
    if (!name) return setIsValid('*Enter name');

    if (!email || !validator.isEmail(email)) return setIsValid('*Please enter a valid email');

    setIsValid('');
    history.replace('/');
  }

  return(
    <div className='signup__container'>
      <i className="far fa-arrow-alt-circle-left fa-1.5x" onClick={() => history.replace('/')}> Back</i>
      <div className='signup'>
        <h2>Create an account</h2>
        <form className='wrapper' onSubmit={handleSubmit}>
          <InputField type="text" name="name" value={state.name} change={handleChange} placeholder='Full Name' autoFocus/>
          <InputField type="text" name="email" value={state.email} change={handleChange} placeholder='Email'/>
          <p className={`errorMsg ${isValid !== '' && 'displayError'}`}>{isValid}</p>
          <div className='btnWrapper'>
            <AuthButton primary>Create Account</AuthButton>
          </div>
          <div className='alternativeBtn'>
            <p>or</p>
            <AuthButton>Continue with Google</AuthButton>
            <p>Already have an account? <span onClick={() => history.push('/login')}>Login</span></p>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Signup;