import React from 'react';

import './About.scss';
import { ReactComponent as FaceBookIcon } from '../../assets/facebook.svg';
import { ReactComponent as TwitterIcon } from '../../assets/twitter.svg';
import { ReactComponent as InstagramIcon } from '../../assets/instagram.svg';

function About() {
  return(
    <div className='about' id='about'>
      <div className='wrapper'>
        <h2>Taste Corner</h2>
        <p>supports us on social medias</p>
        <div className='social__links'>
          <p>Social Links</p>
          <FaceBookIcon className='icon'/>
          <TwitterIcon className='icon'/>
          <InstagramIcon className='icon'/>
        </div>
        <div className='agreement'>
          <p className='terms'>By continuing past this page, you agree to our Terms of Service, Cookie Policy, Privacy Policy and Content Policies. All trademarks are properties of their respective owners. 2008-2021 © Taste Corner™ Pvt Ltd. All rights reserved.</p>
        </div>
      </div>
    </div>
  );
}

export default About;