import React, { Component } from 'react';
import './SearchBar.scss';

class SearchBar extends Component {
  state = {
    search: ''
  }

  handleChange = evt => {
    const { name, value } = evt.target;
    this.setState({ [name]: value });
  }

  handleSubmit = evt => {
    evt.preventDefault();

    this.props.history.push(`/recipe/${this.state.search}`);
    this.setState({ search: '' });
  }

  render() {
    const { search } = this.state;

    return(
      <div className="search">
        <form className="form" onSubmit={this.handleSubmit}>
          <input className="field" type="text" id="search" name="search" placeholder="search for pizza and pasta" onChange={this.handleChange} value={search}/>
          <span>
            <i className="far fa-times-circle fa-2x" onClick={() => this.props.history.goBack()}></i>
          </span>
        </form>
      </div>
    );
  }
}

export default SearchBar;