import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';

import './CartItem.scss';
import Popup from '../Popup/Popup';
import Confirmation from '../Confirmation/Confirmation';
import Backdrop from '../Backdrop/Backdrop';
import { recipePrice, limitTitle } from '../../utility/recipe';

const CartItem = ({ recipe, history, click, wishlist, increase, decrease }) => {
  const [ showPopup, setShowPopup ] = useState(false);

  const togglePopup = () => {
    setShowPopup(!showPopup);
  };

  const viewRecipeDetails = () => {
    if (recipe.title.toLowerCase().includes('pizza' || 'pizzas')) {
      history.push(`/recipe/pizza/${recipe.recipe_id}`);
      return;
    } else {
      history.push(`/recipe/pasta/${recipe.recipe_id}`);
      return;
    }
  };

  return(
    <div>
      {showPopup && <Backdrop close={togglePopup}/>}
      {showPopup && <Popup show={togglePopup}><Confirmation title='Confirm' content='Are you sure, you want to delete?' yes={click} no={togglePopup}/></Popup>}
      <div className='cartItem'>
        <div className='recipe'>
          <div className='imageContainer' onClick={viewRecipeDetails} title='view full details'>
            <img src={recipe.image_url} alt='recipe'/>
          </div>
          <div className='details'>
            <h4>{limitTitle(recipe.title)}</h4>
            <div className='price__quantity'>
            <p className='price'>Price: $ {recipePrice(recipe.recipe_id)}</p>
            {
              wishlist && 
              <p className='quantity'>
                Qty: 
                  <i className="fas fa-caret-left fa-2x cart__icon__arrow" onClick={decrease}></i>
                  {recipe.quantity}
                  <i className="fas fa-caret-right fa-2x cart__icon__arrow" onClick={increase}></i>
              </p>
            }
            </div>
          </div>
        </div>
        <div className='remove'>
          <i className="far fa-times-circle fa-2x" onClick={togglePopup}></i>
        </div>
      </div>
    </div>
  );
};

export default withRouter(CartItem);