import React, { useState, lazy, Suspense } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import './App.scss';
import Navbar from './containers/Navbar/Navbar';
import Home from './containers/Home/Home';
import Wishlist from './containers/Wishlist/Wishlist';
import Favourites from './containers/Favourites/Favourites';
import SearchBar from './containers/SearchBar/SearchBar';
import Menubar from './components/Menubar/Menubar';
import Backdrop from './components/Backdrop/Backdrop';
import Footer from './containers/AboutTasteCorner/About';
import Login from './components/Login/Login';
import Signup from './components/Signup/Signup';
import ErrorBoundary from './components/ErrorBoundary/ErrorBoundary';
import { UserProvider } from './context/UserContext';

const Recipes = lazy(() => import('./containers/Recipes/Recipes'));
const RecipeInfo = lazy(() => import('./containers/RecipeInfo/RecipeInfo'));

function App() {
  const [showMenu, setShowMenu] = useState(false);

  const toggleMenubar = () => {
    setShowMenu(!showMenu);
  };
  
  return(
    <UserProvider>
      <div className="app">
        {
          showMenu && 
          <>
            <Menubar close={toggleMenubar}/>
            <Backdrop close={toggleMenubar}/>
          </>
        }
        <Navbar showMenu={setShowMenu} toggleMenu={toggleMenubar}/>
        <ErrorBoundary>
          <Switch>
            <Route path="/" exact render={routeProps => <Home {...routeProps} showMenu={showMenu} toggle={toggleMenubar}/>}/>
            <Route path="/cart" exact component={Wishlist}/>
            <Route path="/likes" exact component={Favourites}/>
            <Route path="/search" exact component={SearchBar}/>
            <Route path="/signup" exact component={Signup}/>
            <Route path="/login" exact component={Login}/>
            <Suspense fallback={<div>loading...</div>}>
              <Route path="/recipe/:category" exact component={Recipes}/>
              <Route path="/recipe/:category/:recipeId" exact component={RecipeInfo}/>
            </Suspense>
          </Switch>
          <Redirect to="/"/>
        </ErrorBoundary>
        <Footer />
      </div>
    </UserProvider>
  );
}

export default App;