import React from 'react';
import { withRouter } from 'react-router-dom';

import './Navbar.scss';

function Navbar({ history, toggleMenu }) {
  return(
    <nav className='navbar'>
      <div>
        <h3 className='app__name' onClick={() => history.push('/')}>Taste Corner</h3>
      </div>
      <div className='nav__icons'>
        <span className='my__wishlist' onClick={() => history.push('/cart')}>My Wishlist</span>
        <i className="fas fa-search" onClick={() => history.push('/search')}></i>
        <i className="fas fa-bars menu__icon" onClick={toggleMenu}></i>
      </div>
    </nav>
  );
}

export default withRouter(Navbar);