import React from 'react';
import { NavLink } from 'react-router-dom';

import './Menubar.scss';

function Menubar({ close }) {
  return(
    <div className='menubar'>
      <div className='heading'>
        <h3>Taste Corner</h3>
        <i className="far fa-times-circle fa-2x" onClick={close}></i>
      </div>
      <div className='links'>
        <NavLink to="/" className='link' onClick={close}>Home</NavLink>
        <NavLink to="/recipe/pizza" className='link' onClick={close}>Pizza</NavLink>
        <NavLink to="/recipe/pasta" className='link' onClick={close}>Pasta</NavLink>
        <NavLink to="/likes" className='link' onClick={close}>My Favourite</NavLink>
        <NavLink to="/cart" className='link' onClick={close}>Wishlist</NavLink>   
      </div>
    </div>
  );
}

export default Menubar;