import React, { useState } from 'react';
import validator from 'validator';

import './Login.scss';
import AuthButton from '../AuthButton/AuthButton';
import InputField from '../InputFields/InputFields';

function Login({ history }) {
  const [isValid, setIsValid] = useState('');
  const [state, setState] = useState({
    email: '',
    password: ''
  });

  function handleChange(e) {
    setIsValid('');

    const { name, value } = e.target;
    setState(pstate => ({
      ...pstate,
      [name]: value
    }));
  }

  function handleSubmit(e) {
    e.preventDefault();

    let {email, password} = state;
    email = email.toLocaleLowerCase();

    if (!email || !validator.isEmail(email)) return setIsValid('*Please enter a valid email');

    if (!password) return setIsValid('*Enter password');

    setIsValid('');
    history.replace('/');
  }

  return(
    <div className='login__container'>
      <i className="far fa-arrow-alt-circle-left fa-1.5x" onClick={() => history.replace('/')}> Back to Home</i>
      <div className='login'>
        <h2>Login</h2>
        <form className='wrapper' onSubmit={handleSubmit}>
          <InputField type="text" name="email" value={state.email} change={handleChange} placeholder='Email' autoFocus/>
          <InputField type="text" name="password" value={state.password} change={handleChange} placeholder='Password'/>
          <p className={`errorMsg ${isValid !== '' && 'displayError'}`}>{isValid}</p>
          <div className='btnWrapper'>
            <AuthButton type="submit" primary>Login</AuthButton>
          </div>
          <div className='alternativeBtn'>
            <p>Don't have an account? <span onClick={() => history.push('/signup')}>Sign up</span></p>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;